/* Center align and add margin top to .App */

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 40px;
    font-family: Arial, sans-serif;
    max-width: 500px;
    max-height: 2000px;
    text-align: center;
    margin: 40px auto;
    /* Center the .App horizontally */
    font-size: 20px;
}


/* Increase size and style of calculator */

.calculator {
    margin-bottom: 100px;
    background-color: #f7f7f7;
    border-radius: 20px;
    /* Increase border-radius for a smoother look */
    padding: 30px;
    /* Increase padding for more spacing */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    /* Increase box shadow for depth */
    transition: transform 0.3s ease;
    /* Add transition for hover effect */
    width: 300px;
    height: 450px;
    /* Set maximum height */
}


/* Add hover effect to calculator */

.calculator:hover {
    transform: scale(1.02);
    /* Scale up slightly on hover */
}


/* Style input and button */

input[type="number"],
button {
    margin: 20px 0;
    /* Increase margin for more spacing */
    padding: 18px;
    /* Increase padding for larger size */
    border: none;
    border-radius: 50px;
    /* Increase border-radius for a smoother look */
    font-size: 15px;
    /* Increase font size for better visibility */
    display: block;
    /* Display inputs and buttons in block */
    width: calc(100% - 30px);
    /* Set width to 100% minus margin */
}

button {
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}


/* Increase font size and margin for paragraphs */

p {
    margin: 15px 0;
    /* Increase margin for more spacing */
    font-size: 20px;
    /* Increase font size for better readability */
}


/* Style headings */

h1,
h2 {
    color: #007bff;
    font-weight: bold;
    /* Add bold font weight */
}

h1 {
    margin-bottom: 50px;
    /* Increase margin bottom for more spacing */
}


/* Style placeholder text color */

input[type="number"]::placeholder {
    color: #aaa;
}


/* Remove default number input spinner */

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}


/* Add hover effect to buttons */

button:focus,
button:hover {
    outline: none;
    /* Remove outline on hover/focus */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    /* Add box shadow on hover/focus */
}


/* Add pulsating animation effect to buttons */

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}

button:hover {
    animation: pulse 1s infinite;
    /* Apply pulsating animation on hover */
}


/* Media queries for responsiveness */

@media (max-width: 768px) {
    .calculator {
        width: 70%;
        height: auto;
        /* Adjust width for smaller screens */
    }
    .App {
        /* Center the .App horizontally */
        font-size: 15px;
    }
}